import { Component, OnInit } from '@angular/core';
import { NoiQuanLyModel } from '../../../../../shared/models/noi-quan-ly.model';
import { MatDialogRef, MatDialog } from '@angular/material/dialog';
import { AlertDialogComponent } from '../../../../../shared/pages/alert-dialog/alert-dialog.component';
import { MatSnackBar } from '@angular/material/snack-bar';
import { AddNoiTrucThuocService } from '../../quan-ly-noi-truc-thuoc/add-noi-truc-thuoc/add-noi-truc-thuoc.service';
import { Router } from '@angular/router';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-add-noi-truc-thuoc',
  templateUrl: './add-noi-truc-thuoc.component.html',
  styleUrls: ['./add-noi-truc-thuoc.component.scss'],
})
export class AddNoiTrucThuocComponent implements OnInit {
  _noiTrucThuoc = new NoiQuanLyModel();

  constructor(
    private _alertDialog: MatDialog,
    public dialogMainRef: MatDialogRef<AddNoiTrucThuocComponent>,
    private __service: AddNoiTrucThuocService,
    private __snackbar: MatSnackBar,
    private __router: Router
  ) {}

  _title = 'Thêm nơi trực thuộc';
  _titleMainButton = 'Thêm';
  showProgressBar = false;
  ngOnInit(): void {}

  add() {
    this.showProgressBar = true;
    this.__service.add(this._noiTrucThuoc).subscribe(
      (data) => {
        this.showProgressBar = false;
        if (data === 'true') {
          this.__snackbar.open('Thêm thành công', null, {
            duration: 2000,
          });

          this.dialogMainRef.close({
            data: this._noiTrucThuoc,
            result: true,
          });
        } else {
          this.__snackbar.open(data.Message, null, {
            duration: 2000,
          });
        }
      },
      (error) => {
        this.dialogMainRef.close({
          result: false,
        });
        this.showLogin();
        this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
          duration: 2000,
        });
      }
    );
  }

  cancel() {
    var dialogRef = this._alertDialog.open(AlertDialogComponent, {
      disableClose: true,
      width: '17rem',
      data: {
        dataKey: 'Thay đổi của bạn sẽ không được lưu lại. Bạn có muốn hủy?',
        isConfirm: true,
      },
    });

    dialogRef.afterClosed().subscribe((res) => {
      if (res) {
        this.dialogMainRef.close({
          data: this._noiTrucThuoc,
          result: false,
        });
      }
    });
  }
  showLogin() {
    this.__router.navigate(['admin/login']);
  }
}
