import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../../../../environments/environment';
import { NoiQuanLyModel } from '../../../../../shared/models/noi-quan-ly.model';

@Injectable({providedIn: 'root'})

export class AddNoiTrucThuocService {
    constructor(private httpClient: HttpClient) {}

    add(noiTrucThuoc: NoiQuanLyModel): Observable<any> {
        let url = environment.api.url + 'NoiQuanLy/add';
        return this.httpClient.put(url, noiTrucThuoc);
    }
}