import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../../../../environments/environment';
import { NoiQuanLyModel } from '../../../../../shared/models/noi-quan-ly.model';

@Injectable({ providedIn: 'root' })
export class UpdateNoiTrucThuocService {
    constructor( private httpClient: HttpClient ) {}

    update (noiTrucThuoc: NoiQuanLyModel): Observable<any> {
        let url = environment.api.url + 'NoiQuanLy/update';
        return this.httpClient.put(url, noiTrucThuoc);
    }
}