import { Component, OnInit, Inject, Injectable } from '@angular/core';
import { NoiQuanLyModel } from '../../../../../shared/models/noi-quan-ly.model';
import {
  MatDialogRef,
  MAT_DIALOG_DATA,
  MatDialog,
} from '@angular/material/dialog';
import { UpdateNoiTrucThuocService } from './update-noi-truc-thuoc.service';
import { AlertDialogComponent } from '../../../../../shared/pages/alert-dialog/alert-dialog.component';
import { MatSnackBar } from '@angular/material/snack-bar';
import { environment } from 'src/environments/environment';
import { Router } from '@angular/router';

@Component({
  selector: 'app-update-noi-truc-thuoc',
  templateUrl: './update-noi-truc-thuoc.component.html',
  styleUrls: ['./update-noi-truc-thuoc.component.scss'],
})
export class UpdateNoiTrucThuocComponent implements OnInit {
  _noiTrucThuoc = new NoiQuanLyModel();

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    private __service: UpdateNoiTrucThuocService,
    private __snackbar: MatSnackBar,
    public dialogMainRef: MatDialogRef<UpdateNoiTrucThuocComponent>,
    private _alertDialog: MatDialog,
    private __router: Router
  ) {}

  _title = 'Cập nhật';
  _titleMainButton = 'Cập nhật';
  showProgressBar = false;
  ngOnInit(): void {
    if (this.data) {
      if (this.data.noiTrucThuoc) {
        this._noiTrucThuoc = this.data.noiTrucThuoc;
      }
    }
  }

  update() {
    if (this._noiTrucThuoc.TenNoiQuanLy == '') {
      this.__snackbar.open('Vui lòng nhập tên nơi trực thuộc!', null, {
        duration: 2000,
      });
    } else {
      this.showProgressBar = true;
      this.__service.update(this._noiTrucThuoc).subscribe(
        (data) => {
          this.showProgressBar = false;
          if (data === 'true') {
            this.__snackbar.open('Cập nhật thành công', null, {
              duration: 2000,
            });
            let res = this._noiTrucThuoc;
            this.dialogMainRef.close({
              data: this._noiTrucThuoc,
              result: true,
            });
          } else {
            this.__snackbar.open(data, null, {
              duration: 2000,
            });
          }
        },

        (error) => {
          this.dialogMainRef.close({
            result: false,
          });
          this.showLogin();
          this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
            duration: 2000,
          });
        }
      );
    }
  }

  cancel() {
    var dialogRef = this._alertDialog.open(AlertDialogComponent, {
      disableClose: true,
      width: '17rem',
      data: {
        dataKey: 'Thay đổi của bạn không được lưu. Bạn có muốn hủy?',
        isConfirm: true,
      },
    });

    dialogRef.afterClosed().subscribe((res) => {
      if (res) {
        this.dialogMainRef.close({
          data: this._noiTrucThuoc,
          result: false,
        });
      }
    });
  }
  showLogin() {
    this.__router.navigate(['admin/login']);
  }
}
