import { Component, OnInit, ViewChild } from '@angular/core';

import { MatDialog } from '@angular/material/dialog';
import { NoiQuanLyModel } from '../../../../shared/models/noi-quan-ly.model';
import { MatTableDataSource } from '@angular/material/table';
import { MatSort } from '@angular/material/sort';
import { MatPaginator } from '@angular/material/paginator';
import { QuanLyNoiTrucThuocService } from './quan-ly-noi-truc-thuoc.service';
import { AlertDialogComponent } from '../../../../shared/pages/alert-dialog/alert-dialog.component';
import { MatSnackBar } from '@angular/material/snack-bar';
import { AddNoiTrucThuocComponent } from './add-noi-truc-thuoc/add-noi-truc-thuoc.component';
import { UpdateNoiTrucThuocComponent } from './update-noi-truc-thuoc/update-noi-truc-thuoc.component';
import { environment, eMenuAdmin } from 'src/environments/environment';
import { MainAdminComponent } from '../main-nav.component';
import { Router } from '@angular/router';

@Component({
  selector: 'app-quan-ly-noi-truc-thuoc',
  templateUrl: './quan-ly-noi-truc-thuoc.component.html',
  styleUrls: ['./quan-ly-noi-truc-thuoc.component.scss'],
})
export class QuanLyNoiTrucThuocComponent implements OnInit {
  displayedColumns = ['MaNoiQuanLy', 'TenNoiQuanLy', 'TrangThai', 'actions'];

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  constructor(
    private __service: QuanLyNoiTrucThuocService,
    public dialog: MatDialog,
    private __snackbar: MatSnackBar,
    private __router: Router,
    private __mainComponent: MainAdminComponent
  ) {
    localStorage.setItem(
      environment.key_storage.MENU.ADMIN,
      eMenuAdmin[eMenuAdmin.QUAN_LY_NOI_TRUC_THUOC]
    );
    this.__mainComponent.__localStorage = localStorage.getItem(
      environment.key_storage.MENU.ADMIN
    );
  }
  ngOnInit(): void {
    this.loadData();
  }
  showProgressBar = true;
  dataSource: MatTableDataSource<NoiQuanLyModel>;
  _length = 0;
  _noiTrucThuocs: NoiQuanLyModel[];
  showLogin() {
    this.__router.navigate(['admin/login']);
  }
  //thanh trạng thái load

  private getDataNoiTrucThuoc() {
    this.showProgressBar = true;
    this.__service.GetAll().subscribe(
      (data) => {
        this.showProgressBar = false;
        if(data.Message){
          this.__snackbar.open(data.Message, null, {
            duration: 2000,
          });
          return
        }
        this._noiTrucThuocs = data;
        this.dataSource = new MatTableDataSource(this._noiTrucThuocs);
        this._length = this.dataSource.data.length;
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
        this.dataSource.filterPredicate = (
          data: NoiQuanLyModel,
          filter: string
        ) =>
          data.MaNoiQuanLy.toLocaleLowerCase().indexOf(filter) != -1 ||
          data.TenNoiQuanLy.toLocaleLowerCase().indexOf(filter) != -1;

      },

      (error) => {
        this.showProgressBar = false;
        this.showLogin();
        this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
          duration: 2000,
        });
      }
    );
  }

  private loadData() {
    this.getDataNoiTrucThuoc();
  }
  themNoiTrucThuoc() {
    const dialogRef = this.dialog.open(AddNoiTrucThuocComponent, {
      disableClose: true,
      width: '40rem',
      data: {},
    });
  }

  edit(noiTrucThuoc: NoiQuanLyModel) {
    const dialogRef = this.dialog.open(UpdateNoiTrucThuocComponent, {
      disableClose: true,
      width: '40rem',
      data: {
        noiTrucThuoc: noiTrucThuoc,
      },
    });

    dialogRef.afterClosed().subscribe((res) => {
      if (res.result && res.result == true) {
        this.loadData();
      }
    });
  }

  lock(noiTrucThuoc: NoiQuanLyModel) {
    const confirmDialogRef = this.dialog.open(AlertDialogComponent, {
      disableClose: true,
      width: '17rem',
      data: {
        dataKey:
          'Bạn có chắn muốn đóng nơi trực thuộc ' +
          noiTrucThuoc.TenNoiQuanLy +
          '?',
        isConfirm: true,
      },
    });

    confirmDialogRef.afterClosed().subscribe((result) => {
      if (result === true) {
        this.showProgressBar = true;

        this.__service.lock(noiTrucThuoc).subscribe(
          (data) => {
            this.showProgressBar = false;
            if (data === 'true') {
              this.loadData();
              this.__snackbar.open('Đã đóng nơi trực thuộc', null, {
                duration: 2000,
              });
            } else {
              this.__snackbar.open(data, null, {
                duration: 2000,
              });
            }
          },
          (error) => {
            this.showProgressBar = false;
            this.showLogin();
            this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
              duration: 2000,
            });
          }
        );
      }
    });
  }
  unLock(noiTrucThuoc: NoiQuanLyModel) {
    const confirmDialogRef = this.dialog.open(AlertDialogComponent, {
      disableClose: true,
      width: '17rem',
      data: {
        dataKey:
          'Bạn có chắn muốn mở nơi trực thuộc ' +
          noiTrucThuoc.TenNoiQuanLy +
          '?',
        isConfirm: true,
      },
    });

    confirmDialogRef.afterClosed().subscribe((result) => {
      if (result === true) {
        this.showProgressBar = true;

        this.__service.unLock(noiTrucThuoc).subscribe(
          (data) => {
            this.showProgressBar = false;
            if (data === 'true') {
              this.loadData();

              this.__snackbar.open('Đã mở nơi trực thuộc', null, {
                duration: 2000,
              });
            } else {
              this.__snackbar.open(data, null, {
                duration: 2000,
              });
            }
          },
          (error) => {
            this.showProgressBar = false;
            this.showLogin();
            this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
              duration: 2000,
            });
          }
        );
      }
    });
  }

  onSearchChange(searchText: String) {
    this.dataSource.filter = searchText.trim().toLowerCase();
  }
}
