import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../../../environments/environment';
import { NoiQuanLyModel } from '../../../../shared/models/noi-quan-ly.model';

@Injectable({ providedIn: 'root' })
export class QuanLyNoiTrucThuocService {
  constructor(private httpClient: HttpClient) {}

  GetAll(): Observable<any> {
    let url = environment.api.url + 'NoiQuanLy/getAll';
    return this.httpClient.get(url);
  }

  lock(noiTrucThuoc: NoiQuanLyModel): Observable<any> {
    let url = environment.api.url + 'NoiQuanLy/hide';
    return this.httpClient.put(url, noiTrucThuoc);
  }
unLock(noiTrucThuoc: NoiQuanLyModel): Observable<any> {
    let url = environment.api.url + 'NoiQuanLy/show';
    return this.httpClient.put(url, noiTrucThuoc);
  }
}
